@charset "UTF-8";
@import "reset";



:root{ font-size: 62.5%; }

$base: #fff;
$main: #223;
$acce: #fc6;

$mobile: 600px;
$tablet: 1280px;

@mixin max($break) {
    @media screen and ( max-width: $break ){ @content; }
}

@mixin min($break) {
    @media screen and ( min-width: $break ){ @content; }
}

html{ background: $base }
body{
    font: 1.6rem/1.7 "游ゴシック","Yu Gothic",YuGothic,"ヒラギノ角ゴ Pro W3","Hiragino Kaku Gothic ProN","ヒラギノ角ゴ ProN",Meiryo,"メイリオ",Helvetica,Arial,Sans-Serif;
    font-weight: 500;
    font-feature-settings : "pwid";
    color: $main;
    -webkit-font-smoothing: subpixel-antialiased;
    -moz-osx-font-smoothing: grayscale;
}
*:hover{ transition: .2s ease }
ul{
    list-style: none;
    padding: 0;
}

.wrap{
    width: 120rem;
    overflow: auto;
    margin: 0 auto;
}



////////////////////////////////////////////////////////
// responsive
////////////////////////////////////////////////////////
@include max($tablet) {
    :root{ font-size: 50%; }
}
@include max($mobile) {
    :root{ font-size: 40%; }
}



